.container {
  margin: 50px 0;
}

.card {
  margin: 0 2% !important;
}

.infected {
  border-bottom: 10px solid rgba(0, 0, 255, 0.5);
}

.recovered {
  border-bottom: 10px solid rgba(0, 255, 0, 0.5);
}

.deaths {
  border-bottom: 10px solid rgba(255, 0, 0, 0.5);
}

@media (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}

/*! Animation */
.animatedLoader {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
}
.animatedLoader div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #000;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.animatedLoader div:nth-child(1) {
  left: 8px;
  animation: animatedLoader1 0.6s infinite;
}
.animatedLoader div:nth-child(2) {
  left: 8px;
  animation: animatedLoader2 0.6s infinite;
}
.animatedLoader div:nth-child(3) {
  left: 32px;
  animation: animatedLoader2 0.6s infinite;
}
.animatedLoader div:nth-child(4) {
  left: 56px;
  animation: animatedLoader3 0.6s infinite;
}
@keyframes animatedLoader1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes animatedLoader3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes animatedLoader2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
