body {
  background-color: rgb(250, 250, 250);
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.image {
  width: 370px;
  margin: 30px 0 !important;
  cursor: pointer;
}

@media (max-width: 770px) {
  .contaner {
    margin: 0 10% !important;
    padding: 10px !important;
  }

  .image {
    width: 100%;
  }
}

/*! Animation */
.animatedLoader {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
}
.animatedLoader div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #000;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.animatedLoader div:nth-child(1) {
  left: 8px;
  animation: animatedLoader1 0.6s infinite;
}
.animatedLoader div:nth-child(2) {
  left: 8px;
  animation: animatedLoader2 0.6s infinite;
}
.animatedLoader div:nth-child(3) {
  left: 32px;
  animation: animatedLoader2 0.6s infinite;
}
.animatedLoader div:nth-child(4) {
  left: 56px;
  animation: animatedLoader3 0.6s infinite;
}
@keyframes animatedLoader1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes animatedLoader3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes animatedLoader2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
